import { ScrollingModule } from '@angular/cdk/scrolling';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DestroyLotProductComponent } from './destroy-lot-product.component';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DetailComponent } from './detail/detail.component';
import { SearchModule } from 'src/app/sales/components/search/search.module';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { BillType38Module } from 'src/app/sales/components/bill-type/bill-type-38/bill-type-38.module';

const routes: Routes = [
  {
    path: '',
    component: DestroyLotProductComponent
  },
  {
    path: "detail",
    component: DetailComponent
  },
  {
    path: "add",
    component: AddComponent
  },
  {
    path: "edit",
    component: EditComponent
  },
]


@NgModule({
  declarations: [
    DestroyLotProductComponent,
    DetailComponent,
    AddComponent,
    EditComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    ReactiveFormsModule,
    FormsModule,
    ScrollingModule,
    BillType38Module
  ]
})
export class DestroyLotProductModule { }
